  }
  
  @keyframes moveButtonA {
    0%  {transform:  translateY(33px) rotate(-90deg); z-index:0; right:calc(10% - 36px);line-height: 60px;color: $first-color; background-color:$cpdblue}
    90%  {z-index: 0;background-color: $first-color; color:$cpdblue}
    100% {transform:  rotate(0deg);z-index: 10; right: 30px;line-height: 15px;background-color: $first-color; color: $cpdblue}
  }  
  @keyframes moveButtonBackA {
    0%  {transform: rotate(0deg);z-index:10;right: 30px;line-height: 15px;background-color: $first-color; color: $cpdblue} 
    50% {z-index: 0}
    90% {color: $first-color; background-color:$cpdblue}
    100% {transform:  translateY(33px) rotate(-90deg);z-index:0;right:calc(10% - 36px);line-height: 60px;}
    } 
