#infoAsideAccount {
  display: block;
  transform: translateX(-120%) scale(90%);
  z-index: -1;
  position: fixed;
  top: 180px;
  right: 0;
  opacity: 0; 
  background-color: $first-color;
  box-shadow: $boxshadow;
  width: 15vw;
  min-width: 200px;
  height: 400px;
  padding: 10px;
  padding-left: 00px;
  margin-right: 20px;
  font-size: $small;
  border-radius: 5px;
  .account_container {
    display: grid;
    width: 100%;
    place-items: center;
    margin: 10px 0 10px 0;
    #floating-stack {
      background: $first-color;
      height: 380px;
      width: 100%;
      overflow-y: scroll;
      overflow-x: none;
      font-size: $small;
      padding-bottom: 220px;
      dl {
        margin: 0 0 0;
        display: grid;
        grid-template-columns: 2.2rem 1fr;
        align-items: baseline;
        dt {
          text-align: center;
          font-family: $guthaben;
          position: sticky;
          top: 0.5rem;
          left: 0.5rem;
          font-weight: bold;
          background: $third-color;
          color: $first-color;
          height: 35px;
          width: 35px;
          border-radius: 50%;
          line-height: 35px;
          grid-column: 1;
        }
        dd {
          grid-column: 2;
          margin: 0px;
          padding: 10px;
          padding-top: 10px;
          h3 {
            font-weight: 900;
            font-family: $primary-font-bold;
          }
        }
        dd:hover, dd:active {
          background-color: $third-color-o;
          color: black;
        }
      }
      > dl:first-of-type > dd:first-of-type {
        margin-top: 0.25rem;
      }
    }
  }
}

#LC_only {
  font-family: $calli;
  color: $cpdred;
  letter-spacing: -2px;
  text-align: center;
  font-size: 130%;
  text-shadow: 1px 1px rgb(225, 220, 100);
  border-style: solid;
  border-color: rgb(223, 168, 17);
  border-width: 2px;
  background-color: rgb(192, 112, 7);
  background-image: linear-gradient(
    to top left,
    rgb(160, 88, 0),
    rgb(225, 220, 100)
  );
  &.showAccount {animation: show_home_aside 2s ease-in-out forwards}
