@import "../../scss/global.scss";

#updateMain {
  width: 80%;
  max-width: 1280px;
  min-height: 600px;
  margin: 25vh auto;
  box-shadow: $cpdblue 0px 0px 100px 50px inset, 0px 0px 15px 5px white;
}

#UpdateUser {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60%;
  margin: 0 auto;
  gap: 3rem;

  h2{
    display: block;
    margin: 50px auto;
    padding: 20px;
    width:fit-content;
    font-size: $small;
    text-align: center;
    color: $cpdblue;
    border-left: dotted 3px $cpdred;
    border-right: dotted 3px $cpdred;
    border-radius:  20px ;
  }
  >button {
    margin-bottom: 30px;
  }
}

#UpdateUser h1 {
  font-size: 3rem;
  width: 100%;
  text-align: center;
  color: var(--beige-color);
  background-color: var(--dark-transparent-color);
}

#UpdateUser ul {
  font-size: 1.75rem;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.25rem;
  width: 80%;
  background-color: $first-color;
  border-radius: $large;
  padding: 1.5rem 0rem 2rem 1rem;
  box-shadow: $boxshadow;
  transition: width 1s ease-in, font-size 1s ease-in;
  li {
    width: 100%;
    display: grid;
    grid-template-columns: 35% 60%;
    gap: 1rem;
  }

}

#UpdateUser .col2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}

#UpdateUser ul li span .button-dark-green {
  font-size: 1.75rem;
  padding: 0.25rem 1rem;
}

#UpdateUser ul > .button-green {
  margin: 1.5rem auto 0 auto;
  font-size: 2.5rem;
  padding: 0.5rem 1.5rem;
  transition: font-size 1s ease-in;
}

#UpdateUser .col2 input,
#UpdateUser .col2 select {
  font-size: 1.2rem;
  color: $cpdred;
  border-radius: 0.5rem;
  border-color: var(--dark-green-color) var(--middle-green-color)
    var(--middle-green-color) var(--dark-green-color);
  background-color: white;
  padding: 0.25rem;
}

#UpdateUser .col2 select {
  border-width: 2px;
  //font-family: "Poppins";
}

#UpdateUser .col2 select option:checked,
#UpdateUser .col2 select option:hover {
  background-color: var(--middle-green-color);
  color: var(--beige-color);
}

#UpdateUser .col2 input:focus,
#UpdateUser .col2 select:focus {
  background-color: var(--beige-color);
  outline: 3px solid var(--middle-green-color);
}

#UpdateUser .save-icon,
#UpdateUser .edit-icon {
  color: var(--middle-green-color);
  text-align: start;
  font-size: 2.25rem;
  transition: font-size 1s ease-in;
}

#UpdateUser .save-icon:after {
  content: "Speichern";
}

#UpdateUser .edit-icon:after {
  content: "Ändern";
}

#UpdateUser .save-icon:after,
#UpdateUser .edit-icon:after {
  color: var(--beige-color);
  text-shadow: 0 0 0.25rem var(--middle-green-color);
  font-weight: bold;
  transform: translate(-65%, -1rem);
  position: absolute;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

#UpdateUser .save-icon:hover::after,
#UpdateUser .edit-icon:hover::after {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 767px) {
  #UpdateUser {
    gap: 1rem;
  }

  #UpdateUser h1 {
    font-size: 2rem;
  }

  #UpdateUser ul {
    width: 95%;
    padding-left: 0.5rem;
    font-size: 1.5rem;
    gap: 1.5rem;
  }
  #UpdateUser ul li {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0;
    flex-wrap: wrap;
  }

  #UpdateUser .col2 {
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  #UpdateUser .col2 span:first-child {
    width: 100%;
    max-width: max-content;
    overflow: auto;
    text-align: center;
  }

  #UpdateUser .col2 span:last-child {
    width: 1em;
  }

  #UpdateUser .col2 input,
  #UpdateUser .col2 select {
    width: 250px;
  }

  #UpdateUser .col1 {
    font-weight: bold;
  }

  #UpdateUser ul > .button-green {
    font-size: 2rem;
  }
}

@media screen and (min-width: 1024px) {
  #UpdateUser h1 {
    font-size: 4rem;
  }

  #UpdateUser ul {
    width: 80%;
    font-size: 1.2rem;
  }

  #UpdateUser .save-icon,
  #UpdateUser .edit-icon {
    font-size: 1.5rem;
  }
}
