@import "./typography/typography.scss";
@import "./reset/reset.scss";
@import "./variables/variables.scss";


body {
  margin: 0 auto;
  background-color: $cpdblue;
  background-size: 100%;
  background-repeat:repeat-y;
  background-attachment: fixed;
  font-family: $primary-font;
  line-height: 1.2;
  letter-spacing: 0px;
  margin-top:150px;
  overflow-x: hidden; 
  height: auto; 
}

/* header {
  position: fixed;
  top: 0px;
  left: 0px;
} */ 
  
main {
  //display: none;
  width: 80%;
  max-width: 1280px;
  margin: 0 auto;
  z-index: 5;
  position: relative;
  border: solid white 2px;
  border-radius: 20px;
  background-color: $first-color;
  border:2px solid $first-color;
  
  //box-shadow:0px 0px 15px 5px white;
}
    
/* Footer {
  position: relative;
  bottom: 0px;  
} */

.logo_cpd {
  width: 8%;
  margin-left:0px;
  margin-right:10px;
  right:10px;
  align-items:right;
}

.LC {
  font-family: $calli;
  font-weight: bold;
  letter-spacing: -5px;
  line-height: normal;
  font-style: normal;
  color: $cpdred;
  font-size: 2.5rem;
  position: relative;
  bottom: -0.15rem;
}

.C {
  font-family: $calli;
  color: $cpdred;
  font-size: 1.3em;
  position:relative;
  top: 3px;
  /* bottom: -0.14em; */
  text-decoration:none;
  cursor: url(../images/point.png);
}

.Calli {
  font-family: $calli;
  line-height: normal;
  font-style: normal;
  font-size: $normal;
  position:relative;
  bottom: -0.14em;
  text-decoration:none;
  }

.LitCoin {
  font-family: $calli;
  font-style: normal; 
  line-height: normal;
  color: $cpdred; 
  font-size: 1.5em;
  position:relative; 
  bottom: -0.14em;
}

.grad {
  background-image: linear-gradient(rgb(173, 173, 213), rgb(228, 150, 150));
  background-attachment:fixed;
}
 
.Trenner {
  margin: 50px auto;
  width: 5%;
  display:block;
}

.buttonBasics {
  vertical-align: middle;
  width: 60%;
  height: 40px;
  max-width: 150px;
  margin: 0px ;
  font-size: $xsmall;
  padding: 0.5rem 0.5rem;
  background-color: $first-color;
  //background-color: $cpdred;
  color: $cpdblue;
  //color: $first-color;
  box-shadow: $btnshadow;
  border-radius: $radiussmall;
  text-decoration: none;
  border: none;
}
.buttonBasics:hover {
  //background-color: $cpdred;
  background-color: $cpdblue-10;
  //color: $first-color;
}
/* .buttonBasics:focus {
  background-color: $cpdred;
} */

.swal-html{
border-radius: 20px;
background: blue;
}

.swal-text {
  color: $cpdblue;
  border-left: dotted 2px $cpdred;
  border-right: dotted 2px $cpdred;
  border-radius: 10px;
  width: fit-content;
  margin: 0 auto;
  font-size: $normal;
}

.swal-button,
.swal-button--confirm {
  color: $first-color;
  background-color: $cpdblue;
  box-shadow: $btnshadow;
}

.swal-button:hoover,
.swal-button--confirm:hoover {
  color: $cpdblue;
  background-color: $first-color;
  box-shadow: $btnshadow;
}
    
@media (max-width: $tablet){
  body {
    background-image: none;
  }
  .drehmoment {
    display: none;
  }
  main {
    width: 100%;
    margin: 0 0;
  }
  
  #navmain div {
    border: none;
  }
}
  
  
    